import { ComponentFixture, TestBed } from '@angular/core/testing';
import { AppComponent } from './app';
import { PokemonSearchComponent } from './components/pokemon-search/pokemon-search';

describe('AppComponent', () => {
  let component: AppComponent;
  let fixture: ComponentFixture<AppComponent>;

  beforeEach(async () => {
    await TestBed.configureTestingModule({
      imports: [AppComponent],
      providers: []
    }).compileComponents();

    fixture = TestBed.createComponent(AppComponent);
    component = fixture.componentInstance;
    fixture.detectChanges();
  });

  it('should create', () => {
    expect(component).toBeTruthy();
  });

  it('should have the correct title', () => {
    expect(component.title).toBe('Buscador de Pokémon');
  });

  it('should render pokemon search component', () => {
    const compiled = fixture.nativeElement as HTMLElement;
    expect(compiled.querySelector('app-pokemon-search')).toBeTruthy();
  });
});
